<template>
  <div class="fu-month-picker" ref="pickerRef" :style="{ width: formWrapperWidth }">
    <FusionTextInput
      type="text"
      :modelValue="displayValue"
      placeholder="Select month"
      readonly
      @click="toggleCalendar"
      :formWrapperWidth="formWrapperWidth"
      :size="size"
      :error="error"
      :required="required"
      :label="label"
      :disabled="disabled"
      @click.self="closeCalendar"
    >
      <template #right>
        <ChevronDown />
      </template>
    </FusionTextInput>

    <teleport to="body">
      <div
        v-if="calendarOpen"
        class="fu-month-picker__calendar-overlay"
        @click.self="closeCalendar"
      >
        <div
          class="fu-month-picker__calendar"
          :style="calendarPosition"
          ref="calendarRef"
          @click.stop
        >
          <!-- Header -->
          <div class="calendar-header">
            <FusionButton
              variant="ghost"
              size="sm"
              @click="toggleView"
              :icon="ChevronDown"
            >
              <template v-if="viewMode === 'months'">
                {{ currentYear }}
              </template>
              <template v-else> {{ yearRangeStart }} - {{ yearRangeEnd }} </template>
            </FusionButton>

            <div class="flex flex--gap-sm">
              <FusionActionButton :icon="ChevronLeft" size="sm" @click="prevAction" />
              <FusionActionButton :icon="ChevronRight" size="sm" @click="nextAction" />
            </div>
          </div>

          <!-- Months view -->
          <div v-if="viewMode === 'months'" class="calendar-months">
            <div
              v-for="(m, i) in months"
              :key="m"
              class="calendar-month"
              :class="{
                'calendar-month--selected': isSelectedMonth(i),
                'calendar-month--disabled': isDisabledMonth(i),
              }"
              @click="selectMonth(i)"
            >
              {{ m }}
            </div>
          </div>

          <!-- Years view -->
          <div v-else class="calendar-years">
            <div
              v-for="year in yearsRange"
              :key="year"
              class="calendar-year"
              :class="{ 'calendar-year--selected': year === currentYear }"
              @click="selectYear(year)"
            >
              {{ year }}
            </div>
          </div>

          <hr />

          <!-- Quick actions -->
          <div class="flex flex--space flex--gap-md px-2 pb-2">
            <FusionButton variant="outline" @click="selectThisMonth">
              This Month
            </FusionButton>
            <FusionButton variant="outline" @click="clearSelection"> Clear </FusionButton>
          </div>
        </div>
      </div>
    </teleport>
  </div>
</template>

<script setup>
import { ref, computed, watch, nextTick, onBeforeUnmount } from "vue";
import FusionButton from "@/components/button/FusionButton.vue";
import FusionActionButton from "@/components/actionButton/FusionActionButton.vue";
import FusionTextInput from "@/components/TextInput/FusionTextInput.vue";
import { ChevronDown, ChevronRight, ChevronLeft } from "lucide-vue-next";
import dayjs from "dayjs";

const props = defineProps({
  modelValue: String, // "YYYY-MM"
  min: String, // "YYYY-MM"
  max: String, // "YYYY-MM"
  formWrapperWidth: String,
  size: {
    type: String,
    default: "md",
    validator: (v) => ["sm", "md", "lg"].includes(v),
  },
  error: {
    type: [String, null],
    default: null,
  },
  required: {
    type: Boolean,
    default: false,
  },
  disabled: {
    type: Boolean,
    default: false,
  },
  label: String,
});

const emit = defineEmits(["update:modelValue"]);

const calendarOpen = ref(false);
const pickerRef = ref(null);
const calendarRef = ref(null);

// Vista: 'months' o 'years'
const viewMode = ref("months");

// Mantiene el aÃ±o que se estÃ¡ viendo (para mostrar meses o aÃ±os)
const currentYear = ref(dayjs().year());

const months = [
  "Jan",
  "Feb",
  "Mar",
  "Apr",
  "May",
  "Jun",
  "Jul",
  "Aug",
  "Sep",
  "Oct",
  "Nov",
  "Dec",
];

// Calcula el rango de aÃ±os para vista aÃ±o (p.ej 12 aÃ±os alrededor)
const yearsRangeCount = 12;
const yearRangeStart = computed(
  () => currentYear.value - (currentYear.value % yearsRangeCount)
);
const yearRangeEnd = computed(() => yearRangeStart.value + yearsRangeCount - 1);
const yearsRange = computed(() => {
  const start = yearRangeStart.value;
  return Array.from({ length: yearsRangeCount }, (_, i) => start + i);
});

// Convierte modelValue a objeto dayjs para comparaciÃ³n
const selectedMonth = computed(() => {
  if (!props.modelValue) return null;
  const parsed = dayjs(props.modelValue, "YYYY-MM", true);
  return parsed.isValid() ? parsed : null;
});

function isSelectedMonth(monthIndex) {
  if (!selectedMonth.value) return false;
  return (
    selectedMonth.value.month() === monthIndex &&
    selectedMonth.value.year() === currentYear.value
  );
}

function isDisabledMonth(monthIndex) {
  const date = dayjs(
    `${currentYear.value}-${(monthIndex + 1).toString().padStart(2, "0")}`,
    "YYYY-MM"
  );
  if (props.min && date.isBefore(dayjs(props.min, "YYYY-MM"), "month")) return true;
  if (props.max && date.isAfter(dayjs(props.max, "YYYY-MM"), "month")) return true;
  return false;
}

function selectYear(year) {
  currentYear.value = year;
  viewMode.value = "months";
}
function selectMonth(monthIndex) {
  if (isDisabledMonth(monthIndex)) return;
  const selected = dayjs(
    `${currentYear.value}-${(monthIndex + 1).toString().padStart(2, "0")}`,
    "YYYY-MM"
  );
  // Emit in "MMM, YYYY" format
  emit("update:modelValue", selected.format("MMM, YYYY"));
  calendarOpen.value = false;
}

function selectThisMonth() {
  const now = dayjs();
  currentYear.value = now.year();
  emit("update:modelValue", now.format("MMM, YYYY"));
  calendarOpen.value = false;
}

function toggleCalendar() {
  if (props.disabled) return;
  calendarOpen.value = !calendarOpen.value;

  if (calendarOpen.value) {
    // Ajustar el aÃ±o actual segÃºn el modelValue cuando se abre calendario
    if (selectedMonth.value) currentYear.value = selectedMonth.value.year();

    nextTick(() => {
      positionCalendar();
      window.addEventListener("resize", positionCalendar);
      window.addEventListener("click", onClickOutside);
    });
  } else {
    window.removeEventListener("resize", positionCalendar);
    window.removeEventListener("click", onClickOutside);
  }
}

function closeCalendar() {
  calendarOpen.value = false;
  window.removeEventListener("resize", positionCalendar);
  window.removeEventListener("click", onClickOutside);
}

function onClickOutside(e) {
  if (!pickerRef.value?.contains(e.target) && !calendarRef.value?.contains(e.target)) {
    closeCalendar();
  }
}

const calendarPosition = ref({
  position: "absolute",
  top: "0px",
  left: "0px",
  zIndex: 9999,
});

function positionCalendar() {
  if (!pickerRef.value || !calendarRef.value) return;

  const pickerRect = pickerRef.value.getBoundingClientRect();
  const calendarRect = calendarRef.value.getBoundingClientRect();

  const spaceBelow = window.innerHeight - pickerRect.bottom;
  const spaceAbove = pickerRect.top;

  const scrollY = window.scrollY || window.pageYOffset;
  const scrollX = window.scrollX || window.pageXOffset;

  let top;

  if (spaceBelow < calendarRect.height && spaceAbove > calendarRect.height) {
    top = pickerRect.top + scrollY - calendarRect.height - 6;
  } else {
    top = pickerRect.bottom + scrollY + 6;
  }

  calendarPosition.value = {
    position: "absolute",
    top: `${top}px`,
    left: `${pickerRect.left + scrollX}px`,
    zIndex: 9999,
  };
}

function toggleView() {
  viewMode.value = viewMode.value === "months" ? "years" : "months";
}

function prevAction() {
  if (viewMode.value === "months") {
    currentYear.value--;
  } else {
    currentYear.value = Math.max(yearRangeStart.value - yearsRangeCount, 0);
  }
}

function nextAction() {
  if (viewMode.value === "months") {
    currentYear.value++;
  } else {
    currentYear.value = yearRangeEnd.value + 1;
  }
}

function clearSelection() {
  emit("update:modelValue", null);
  calendarOpen.value = false;
}

const displayValue = computed(() => {
  return props.modelValue || "";
});

onBeforeUnmount(() => {
  window.removeEventListener("resize", positionCalendar);
  window.removeEventListener("click", onClickOutside);
});
</script>

<style scoped>
@import url("../style/style.scss");
</style>
