<template>
  <div class="fu-status-dropdown" ref="dropdown">
    <div v-if="label" class="fu-status-dropdown__label-text">
      {{ label }}
    </div>

    <button class="fu-status-dropdown__button" @click="toggleDropdown">
      <span v-if="selected">
        <component
          v-if="selected.icon"
          :is="selected.icon"
          class="fu-status-dropdown__icon"
        />
        <span
          v-else-if="selected.color"
          class="fu-status-dropdown__dot"
          :style="{ backgroundColor: selected.color }"
        />
      </span>

      <span
        class="fu-status-dropdown__label"
        :class="{ 'fu-status-dropdown__placeholder': !selected }"
      >
        {{ selected?.label || placeholder }}
      </span>

      <ChevronDown class="fu-status-dropdown__chevron" />
    </button>

    <!-- Teleported menu -->
    <Teleport to="body">
      <transition name="fade">
        <ul
          v-if="dropdownOpen"
          ref="menuRef"
          class="fu-status-dropdown__menu"
          :style="menuStyle"
        >
          <li
            v-for="option in options"
            :key="option.label"
            class="fu-status-dropdown__item"
            @click="selectStatus(option)"
          >
            <component
              v-if="option.icon"
              :is="option.icon"
              class="fu-status-dropdown__icon"
            />
            <span
              v-else-if="option.color"
              class="fu-status-dropdown__dot"
              :style="{ backgroundColor: option.color }"
            />
            <span class="fu-status-dropdown__item-label">
              {{ option.label }}
            </span>
          </li>
        </ul>
      </transition>
    </Teleport>
  </div>
</template>

<script setup lang="ts">
import { ChevronDown } from "lucide-vue-next";
import { ref, watch, onMounted, onBeforeUnmount, reactive, nextTick } from "vue";
import type { Component } from "vue";

interface Option {
  label: string;
  color?: string;
  icon?: Component;
}

interface Props {
  modelValue: Option | null;
  options: Option[];
  align?: "left" | "center" | "right";
  label?: string;
  placeholder?: string;
}

const props = withDefaults(defineProps<Props>(), {
  align: "left",
  label: "",
  placeholder: "Select Status",
});

const emit = defineEmits(["update:modelValue"]);

const dropdownOpen = ref(false);
const dropdown = ref<HTMLElement | null>(null);
const menuRef = ref<HTMLElement | null>(null);
const selected = ref<Option | null>(props.modelValue || null);

const menuStyle = reactive<Record<string, string>>({
  position: "absolute",
  visibility: "hidden",
  opacity: "0",
  zIndex: "9999",
});

watch(
  () => props.modelValue,
  (val) => (selected.value = val)
);

/* --------------------------------------------------
   Smart positioning (auto flip)
-------------------------------------------------- */
function calculatePosition() {
  if (!dropdown.value || !menuRef.value) return;

  const triggerRect = dropdown.value.getBoundingClientRect();
  const menuRect = menuRef.value.getBoundingClientRect();
  const viewportHeight = window.innerHeight;

  const spaceBelow = viewportHeight - triggerRect.bottom;
  const spaceAbove = triggerRect.top;

  const openUpwards = spaceBelow < menuRect.height && spaceAbove > spaceBelow;

  // Horizontal alignment
  let left = triggerRect.left + window.scrollX;
  let transform = "none";

  if (props.align === "center") {
    left += triggerRect.width / 2;
    transform = "translateX(-50%)";
  }

  if (props.align === "right") {
    left = triggerRect.right + window.scrollX;
    transform = "translateX(-100%)";
  }

  menuStyle.left = `${left}px`;
  menuStyle.transform = transform;
  menuStyle.minWidth = `${triggerRect.width}px`;

  // Vertical positioning
  if (openUpwards) {
    menuStyle.top = `${triggerRect.top + window.scrollY - menuRect.height - 6}px`;
  } else {
    menuStyle.top = `${triggerRect.bottom + window.scrollY + 6}px`;
  }

  menuStyle.visibility = "visible";
  menuStyle.opacity = "1";
}

/* --------------------------------------------------
   Actions
-------------------------------------------------- */
const toggleDropdown = async () => {
  dropdownOpen.value = !dropdownOpen.value;

  if (dropdownOpen.value) {
    await nextTick();
    calculatePosition();
    await nextTick();
    calculatePosition(); // second pass for safety
  }
};

const selectStatus = (option: Option) => {
  selected.value = option;
  emit("update:modelValue", option);
  dropdownOpen.value = false;
};

/* --------------------------------------------------
   Close helpers
-------------------------------------------------- */
const handleClickOutside = (e: MouseEvent) => {
  const target = e.target as Node;
  if (
    dropdownOpen.value &&
    dropdown.value &&
    menuRef.value &&
    !dropdown.value.contains(target) &&
    !menuRef.value.contains(target)
  ) {
    dropdownOpen.value = false;
  }
};

const handleViewportChange = () => {
  if (dropdownOpen.value) dropdownOpen.value = false;
};

const handleKeyDown = (e: KeyboardEvent) => {
  if (e.key === "Escape") dropdownOpen.value = false;
};

onMounted(() => {
  document.addEventListener("click", handleClickOutside);
  window.addEventListener("resize", handleViewportChange);
  window.addEventListener("scroll", handleViewportChange, true);
  document.addEventListener("keydown", handleKeyDown);
});

onBeforeUnmount(() => {
  document.removeEventListener("click", handleClickOutside);
  window.removeEventListener("resize", handleViewportChange);
  window.removeEventListener("scroll", handleViewportChange, true);
  document.removeEventListener("keydown", handleKeyDown);
});
</script>

<style scoped lang="scss">
@import "./FusionStatusDropdown.scss";
</style>
