<template>
  <div class="fu-status-dropdown" ref="dropdownRef">
    <div v-if="label" class="fu-status-dropdown__label-text">
      {{ label }}
    </div>

    <!-- BUTTON VARIANT -->
    <div v-if="variant === 'button'">
      <button
        class="fu-status-dropdown__button"
        :class="[`fu-input--${size}`, { 'fu-input--error': error }]"
        @click="toggleDropdown"
      >
        <div v-if="multiple">
          <template v-if="selectedItems.length">
            <span v-for="item in selectedItems" :key="item.value" class="fu-tag">
              <component v-if="item.type === 'icon'" :is="item.icon" size="14" />
              <img
                v-else-if="item.type === 'image'"
                :src="item.imageUrl"
                class="fu-option-image"
                alt="User Image"
              />
              {{ item.label }}
              <span class="fu-tag__remove" @click.stop="removeItem(item)">Ã—</span>
            </span>
          </template>
          <span v-else class="fu-placeholder">{{ placeholder }}</span>
        </div>

        <div v-else class="flex flex--center flex--gap-md">
          <component
            v-if="selectedOption?.type === 'icon'"
            :is="selectedOption.icon"
            size="16"
          />
          <img
            v-else-if="selectedOption?.type === 'image'"
            :src="selectedOption.imageUrl"
            class="fu-option-image"
            alt="User Image"
          />
          <span>{{ selectedOption?.label || placeholder }}</span>
        </div>

        <svg class="fu-status-dropdown__chevron" viewBox="0 0 20 20">
          <path d="M6 8l4 4 4-4" stroke="currentColor" stroke-width="1.5" />
        </svg>
      </button>
    </div>

    <!-- INPUT VARIANT -->
    <div v-else class="fu-status-dropdown__input-trigger">
      <FusionTextInput
        :size="size"
        readonly
        variant="outline"
        :formWrapperWidth="formWrapperWidth"
        :placeholder="selectedOption?.label || placeholder"
        @focus="toggleDropdown"
        @click="toggleDropdown"
      />
    </div>

    <!-- DROPDOWN -->
    <teleport to="body">
      <transition name="fade">
        <div
          v-if="isOpen"
          class="fu-status-dropdown__menu"
          :style="dropdownStyle"
          ref="menuRef"
        >
          <!-- SEARCH -->
          <div v-if="searchable" class="fu-search-wrapper">
            <FusionTextInput
              ref="searchInputRef"
              v-model="searchQuery"
              type="text"
              :placeholder="searchPlaceholder"
              :size="size"
              formWrapperWidth="100%"
            >
              <template #right>
                <button class="fu-search-clear" @click="searchQuery = ''">Ã—</button>
              </template>
            </FusionTextInput>
          </div>

          <!-- OPTIONS -->
          <div
            v-if="filteredOptions.length"
            class="fu-options-scroll scrollbar__control customScrollBar"
          >
            <div
              v-for="item in filteredOptions"
              :key="item.value"
              class="fu-status-dropdown__item"
              @click="selectItem(item)"
            >
              <component v-if="item.type === 'icon'" :is="item.icon" size="16" />
              <img
                v-else-if="item.type === 'image'"
                :src="item.imageUrl"
                class="fu-option-image"
              />
              <span>{{ item.label }}</span>
            </div>

            <div class="fu-status-dropdown__slot-actions" ref="actionsRef">
              <slot name="actions" />
            </div>
          </div>

          <!-- EMPTY -->
          <div v-else class="fu-status-dropdown__empty">
            {{ noResultsText }}
          </div>
        </div>
      </transition>
    </teleport>

    <span v-if="error" class="fu-input-error">
      {{ error }}
    </span>
  </div>
</template>

<script setup lang="ts">
import { ref, computed, watch, onMounted, onBeforeUnmount, nextTick } from "vue";
import FusionTextInput from "@/components/TextInput/FusionTextInput.vue";

interface Option {
  label: string;
  value: string | number;
  type?: "icon" | "image";
  icon?: any;
  imageUrl?: string;
}

interface Props {
  options: Option[];
  modelValue?: Option | Option[] | null;
  multiple?: boolean;
  placeholder?: string;
  searchable?: boolean;
  noResultsText?: string;
  searchPlaceholder?: string;
  label?: string;
  variant?: "button" | "input";
  size?: "sm" | "md" | "lg";
  formWrapperWidth?: string;
  error?: string | null;

  async?: boolean;
  minSearchLength?: number;
  loading?: boolean;
}

const props = withDefaults(defineProps<Props>(), {
  modelValue: null,
  multiple: false,
  placeholder: "Select...",
  searchable: true,
  noResultsText: "No results found",
  searchPlaceholder: "Search...",
  label: "",
  variant: "button",
  size: "md",
  formWrapperWidth: "",
  error: null,

  async: false,
  minSearchLength: 2,
  loading: false,
});

const emit = defineEmits(["update:modelValue", "search"]);

const isOpen = ref(false);
const searchQuery = ref("");
const dropdownRef = ref<HTMLElement | null>(null);
const menuRef = ref<HTMLElement | null>(null);
const actionsRef = ref<HTMLElement | null>(null);
const searchInputRef = ref<any>(null);
const dropdownStyle = ref<Record<string, string>>({});

const selectedOption = ref<Option | null>(null);
const selectedItems = ref<Option[]>([]);

const isAsync = computed(() => props.async === true);

// sync model
watch(
  () => props.modelValue,
  (val) => {
    if (props.multiple && Array.isArray(val)) selectedItems.value = val;
    else selectedOption.value = val as Option | null;
  },
  { immediate: true }
);

// async search
watch(searchQuery, (query) => {
  if (!isAsync.value) return;
  if (query.length < props.minSearchLength) return;
  emit("search", query);
});

// filtering
const filteredOptions = computed(() => {
  if (isAsync.value) return props.options;
  if (!searchQuery.value) return props.options;
  return props.options.filter((o) =>
    o.label.toLowerCase().includes(searchQuery.value.toLowerCase())
  );
});

function toggleDropdown() {
  isOpen.value = !isOpen.value;

  if (isOpen.value) {
    nextTick(() => {
      const trigger = dropdownRef.value?.querySelector("button, input") as HTMLElement;
      if (!trigger || !menuRef.value) return;

      const rect = trigger.getBoundingClientRect();
      dropdownStyle.value = {
        position: "absolute",
        top: `${rect.bottom + window.scrollY}px`,
        left: `${rect.left + window.scrollX}px`,
        width: `${rect.width}px`,
        zIndex: "9999",
      };

      // ðŸ”¥ AUTO FOCUS SEARCH INPUT
      if (props.searchable) {
        nextTick(() => {
          const inputEl =
            searchInputRef.value?.$el?.querySelector("input") ||
            menuRef.value?.querySelector("input");

          inputEl?.focus();
          inputEl?.select();
        });
      }
    });
  }
}

function selectItem(item: Option) {
  if (props.multiple) {
    const exists = selectedItems.value.find((i) => i.value === item.value);
    selectedItems.value = exists
      ? selectedItems.value.filter((i) => i.value !== item.value)
      : [...selectedItems.value, item];
    emit("update:modelValue", selectedItems.value);
  } else {
    selectedOption.value = item;
    emit("update:modelValue", item);
    isOpen.value = false;
  }
}

function removeItem(item: Option) {
  selectedItems.value = selectedItems.value.filter((i) => i.value !== item.value);
  emit("update:modelValue", selectedItems.value);
}

function closeDropdown(e: MouseEvent) {
  if (
    dropdownRef.value?.contains(e.target as Node) ||
    menuRef.value?.contains(e.target as Node)
  )
    return;
  isOpen.value = false;
}

onMounted(() => document.addEventListener("click", closeDropdown));
onBeforeUnmount(() => document.removeEventListener("click", closeDropdown));
</script>

