<template>
  <label
    class="fu-radio"
    :class="[`fu-radio--${size}`, { 'is-checked': isChecked, 'is-disabled': disabled }]"
  >
    <input
      type="radio"
      class="fu-radio__input"
      :value="value"
      :disabled="disabled"
      :checked="isChecked"
      @change="emit('update:modelValue', value)"
    />

    <!-- Outer Circle -->
    <span class="fu-radio__control">
      <span class="fu-radio__dot" v-if="isChecked" />
    </span>

    <!-- Label -->
    <span v-if="$slots.default" class="fu-radio__label">
      <slot />
    </span>
  </label>
</template>

<script setup lang="ts">
import { computed } from "vue";

type Size = "xs" | "sm" | "md" | "lg" | "xl";

interface Props {
  modelValue?: string | number;
  value: string | number;
  size?: Size;
  disabled?: boolean;
}

const props = withDefaults(defineProps<Props>(), {
  size: "md",
  disabled: false,
});

const emit = defineEmits(["update:modelValue"]);

const model = computed<string | number>({
  get: () => props.modelValue as string | number,
  set: (val: string | number) => emit("update:modelValue", val),
});

const isChecked = computed(() => model.value === props.value);
</script>

