    transition: all 0.2s ease;
  }

  &__label {
    font-weight: 500;
  }

  /* Sizes */
  &--xs &__control {
    width: 14px;
    height: 14px;
  }
  &--xs &__dot {
    width: 6px;
    height: 6px;
  }

  &--sm &__control {
    width: 18px;
