<template>
  <div
    class="fu-attachment-item"
    role="button"
    tabindex="0"
    @click="handleClick"
    @keypress.enter="handleClick"
  >
    <!-- Left: File Preview or Icon -->
    <div class="fu-attachment-left flex flex--gap-md flex--align-center">
      <div class="fu-attachment-preview">
        <!-- Image Preview -->
        <img
          v-if="isImage"
          :src="fileUrl"
          :alt="fileName"
          class="fu-attachment-thumbnail"
        />

        <!-- Video Icon -->
        <Video v-else-if="isVideo" class="fu-attachment-icon" :size="20" />

        <!-- Document Icon -->
        <FileText v-else class="fu-attachment-icon" :size="20" />
      </div>

      <!-- File Info -->
      <div class="fu-attachment-info flex flex--column">
        <span class="fu-attachment-title">{{ fileName }}</span>
        <span class="fu-attachment-meta">{{ fileSize }}</span>
      </div>
    </div>

    <!-- Right Section -->
    <div class="fu-attachment-right flex flex--align-center flex--gap-md">
      <!-- User & Time -->
      <div class="flex flex--column flex--align-center">
        <FuAvatar
          :src="userAvatar || undefined"
          :name="userName"
          :alt="userName"
          size="xs"
          :show-status="false"
        />
        <span class="fu-attachment-time">{{ timestamp }}</span>
      </div>

      <!-- Actions -->
      <div v-if="actions?.length" class="fu-attachment-actions" @click.stop>
        <DropdownMenu :actions="actions" align="right">
          <template #trigger>
            <FusionActionButton :icon="EllipsisVertical" variant="ghost" size="sm" />
          </template>
        </DropdownMenu>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import { computed } from "vue";
import FuAvatar from "@/components/avatar/FuAvatar.vue";
import DropdownMenu from "@/components/dropdownMenu/DropdownMenu.vue";
import FusionActionButton from "@/components/actionButton/FusionActionButton.vue";
import { FileText, Video, EllipsisVertical } from "lucide-vue-next";

interface Action {
  label: string;
  icon?: any;
  onClick?: () => void;
}

interface Props {
  id: string | number;
  fileName: string;
  fileUrl: string;
  fileSize?: string;
  timestamp: string;
  userName: string;
  userAvatar?: string | null;
  actions?: Action[];
}

const props = withDefaults(defineProps<Props>(), {
  fileSize: "",
  userAvatar: null,
  actions: () => [],
});

const emit = defineEmits<{
  (e: "click", id: string | number): void;
}>();

function handleClick() {
  emit("click", props.id);
}

const isImage = computed(() => /\.(png|jpe?g|gif|webp|svg)$/i.test(props.fileName));

const isVideo = computed(() => /\.(mp4|mov|avi|webm)$/i.test(props.fileName));
</script>

