<template>
  <div
    class="fu-upload"
    :class="{ dragging: isDragging }"
    @click="triggerFileSelect"
    @dragover.prevent="isDragging = true"
    @dragleave.prevent="isDragging = false"
    @drop.prevent="handleDrop"
  >
    <div class="fu-upload__content">
      <!-- File Previews -->
      <div v-if="previews.length" class="fu-upload__previews">
        <div
          v-for="(item, index) in previews"
          :key="item.id"
          class="fu-upload__preview-item"
        >
          <button
            class="fu-upload__remove"
            @click.stop="removeFile(index)"
          >
            âœ•
          </button>

          <img
            v-if="item.isImage"
            :src="item.src"
            class="fu-upload__preview-img"
            alt="Preview"
          />

          <div v-else class="fu-upload__file-fallback">
            <MonitorSmartphone :size="20" />
            <span>{{ item.file.name }}</span>
          </div>
        </div>
      </div>

      <!-- Upload Prompt -->
      <div v-else class="fu-upload__prompt">
        <MonitorSmartphone class="fu-upload__icon" :size="22" />
        <slot name="description">
          <p class="fu-upload__text">
            Drag & drop files or click to browse
          </p>
        </slot>
      </div>

      <!-- Hidden File Input -->
      <input
        ref="fileInput"
        type="file"
        class="fu-upload__input"
        :multiple="multiple"
        :accept="accept"
        @change="handleFileChange"
      />
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref } from "vue";
import { MonitorSmartphone } from "lucide-vue-next";

/* ----------------------------------
 * Props & Emits
 * ---------------------------------- */
const props = defineProps({
  multiple: { type: Boolean, default: false },
  accept: { type: String, default: "*" },
  maxFiles: { type: Number, default: Infinity },
  maxFileSizeMB: { type: Number, default: Infinity },
});

const emit = defineEmits<{
  (e: "filesSelected", files: File[]): void;
  (e: "uploadError", message: string): void;
}>();

/* ----------------------------------
 * State
 * ---------------------------------- */
const isDragging = ref(false);
const fileInput = ref<HTMLInputElement | null>(null);

/* Real files */
const files = ref<File[]>([]);

/* Preview metadata */
const previews = ref<
  {
    id: string;
    src: string;
    file: File;
    isImage: boolean;
  }[]
>([]);

/* ----------------------------------
 * Helpers
 * ---------------------------------- */
function generateId() {
  if (typeof crypto !== "undefined" && crypto.randomUUID) {
    return crypto.randomUUID();
  }

  return `file_${Date.now()}_${Math.random().toString(36).slice(2, 9)}`;
}

/* ----------------------------------
 * Handlers
 * ---------------------------------- */
function triggerFileSelect() {
  fileInput.value?.click();
}

function handleFileChange(event: Event) {
  const target = event.target as HTMLInputElement;
  if (!target.files?.length) return;

  addFiles(target.files);
  target.value = ""; // allow re-selecting same file
}

function handleDrop(event: DragEvent) {
  isDragging.value = false;
  const droppedFiles = event.dataTransfer?.files;
  if (!droppedFiles?.length) return;

  addFiles(droppedFiles);
}

function addFiles(fileList: FileList) {
  const incoming = Array.from(fileList);

  if (!props.multiple) {
    files.value = [];
    previews.value = [];
  }

  for (const file of incoming) {
    // Check max file size (in bytes)
    const maxBytes = props.maxFileSizeMB * 1024 * 1024;
    if (file.size > maxBytes) {
      const msg = `File "${file.name}" exceeds max size of ${props.maxFileSizeMB} MB.`;
      emit("uploadError", msg);
      continue; // skip this file
    }

    // Check max number of files allowed
    if (files.value.length >= props.maxFiles) {
      const msg = `Maximum of ${props.maxFiles} files allowed.`;
      emit("uploadError", msg);
      break; // stop adding further files
    }

    files.value.push(file);

    const id = generateId();
    const isImage = file.type.startsWith("image/");

    if (isImage) {
      const reader = new FileReader();
      reader.onload = (e) => {
        previews.value.push({
          id,
          src: e.target?.result as string,
          file,
          isImage: true,
        });
      };
      reader.readAsDataURL(file);
    } else {
      previews.value.push({
        id,
        src: "",
        file,
        isImage: false,
      });
    }
  }

  emit("filesSelected", files.value);
}


function removeFile(index: number) {
  files.value.splice(index, 1);
  previews.value.splice(index, 1);
  emit("filesSelected", files.value);
}
</script>

