<template>
  <div class="fu-dropdown" ref="dropdown">
    <!-- Main button -->
    <button class="fu-dropdown__button" @click="toggleDropdown">
      {{ buttonText }}
      <component v-if="buttonIcon" :is="buttonIcon" class="fu-dropdown__icon" />
    </button>

    <!-- Teleported dropdown -->
    <Teleport to="body">
      <ul
        v-if="dropdownOpen"
        class="fu-dropdown__menu"
        :class="[`fu-dropdown__menu--${align}`, { show: dropdownOpen }]"
        :style="menuStyle"
        ref="menuRef"
      >
        <li v-for="action in actions" :key="action.label">
          <a class="fu-dropdown__item" @click="performClick(action)">
            <component v-if="action.icon" :is="action.icon" class="fu-dropdown__icon" />
            {{ action.label }}
          </a>
        </li>
      </ul>
    </Teleport>
  </div>
</template>

<script setup lang="ts">
import { ref, onMounted, onBeforeUnmount, nextTick, watch } from "vue";
import type { Component } from "vue";

interface Action {
  label: string;
  icon?: Component;
  onClick?: () => void;
}

interface Props {
  buttonText: string;
  buttonIcon?: Component;
  actions: Action[];
  align?: "left" | "center" | "right";
}

const props = withDefaults(defineProps<Props>(), {
  align: "right",
});

const dropdownOpen = ref(false);
const dropdown = ref<HTMLElement | null>(null);
const menuRef = ref<HTMLElement | null>(null);
const menuStyle = ref<Record<string, string>>({});


let activeDropdown: HTMLElement | null = null;

function toggleDropdown() {
  dropdownOpen.value = !dropdownOpen.value;

  if (dropdownOpen.value) {
    if (activeDropdown && activeDropdown !== dropdown.value) {
      const event = new CustomEvent("close-other-dropdowns");
      document.dispatchEvent(event);
    }
    activeDropdown = dropdown.value;
    nextTick(updateMenuPosition);
  }
}

function updateMenuPosition() {
  const button = dropdown.value?.querySelector("button");
  if (!button || !menuRef.value) return;

  const rect = button.getBoundingClientRect();
  const menuWidth = menuRef.value.offsetWidth;

  const positions: Record<string, string> = {
    left: `${rect.left}px`,
    top: `${rect.bottom + 4}px`,
  };

  if (props.align === "right") {
    positions.left = `${rect.right - menuWidth}px`;
  } else if (props.align === "center") {
    positions.left = `${rect.left + rect.width / 2 - menuWidth / 2}px`;
  }

  menuStyle.value = {
    position: "absolute",
    ...positions,
    zIndex: "1000",
  };
}

function performClick(action: Action) {
  action.onClick?.();
  dropdownOpen.value = false;
}

function handleClickOutside(event: MouseEvent) {
  if (
    dropdown.value &&
    !dropdown.value.contains(event.target as Node) &&
    menuRef.value &&
    !menuRef.value.contains(event.target as Node)
  ) {
    dropdownOpen.value = false;
  }
}

function closeOtherDropdowns() {
  dropdownOpen.value = false;
}

onMounted(() => {
  document.addEventListener("click", handleClickOutside);
  document.addEventListener("close-other-dropdowns", closeOtherDropdowns);
});

onBeforeUnmount(() => {
  document.removeEventListener("click", handleClickOutside);
  document.removeEventListener("close-other-dropdowns", closeOtherDropdowns);
});
</script>

<style scoped lang="scss">
@import url("./FusionDropdownButton.scss");
</style>
