<template>
    <div class="edf-container">
        <label v-if="label" class="edf-label">{{ label }}</label>

        <div class="edf-wrapper" :class="[`edf--${variant}`]" @mouseenter="hover = true" @mouseleave="hover = false"
            ref="container">
            <!-- TEXT -->
            <span class="edf-text">{{ text }}</span>

            <!-- ACTION BUTTON -->
            <FusionActionButton v-if="hover" class="edf-edit-btn" :icon="Edit" size="sm" variant="subtle"
                @click="triggerEdit" />
        </div>
    </div>
</template>

<script setup lang="ts">
import { ref } from "vue";
import FusionActionButton from "@/components/actionButton/FusionActionButton.vue";
import { Edit } from "lucide-vue-next";

const props = withDefaults(defineProps<{
    text: string;
    label?: string;
    variant?: "solid" | "outline" | "ghost";
}>(), {
    variant: "solid",
    label: ""
});

const emit = defineEmits<{
    (e: "edit", event: MouseEvent): void;
}>();

const hover = ref(false);

function triggerEdit(event: MouseEvent) {
    emit("edit", event);
}
</script>

