<template>
  <Teleport to="body">
    <div v-if="isVisible" class="fu-preview-backdrop">
      <div class="fu-preview-modal">
        <!-- HEADER 1 -->
        <header class="fu-preview-header">
          <div class="fu-preview-header__left">
            <slot name="header-left" />
          </div>

          <div class="fu-preview-header__right">
            <slot name="header-right">
              <FusionButton
                variant="subtle"
                size="sm"
                text="Close preview"
                @click="onClose"
              />
            </slot>
          </div>
        </header>

        <!-- HEADER 2 -->
        <header class="fu-preview-subheader">
          <slot name="subheader" />
        </header>

        <!-- BODY -->
        <main class="fu-preview-body">
          <slot />
        </main>
      </div>
    </div>
  </Teleport>
</template>

<script setup lang="ts">
import { onMounted, onBeforeUnmount, watch } from "vue";
import FusionButton from "../button/FusionButton.vue";

const props = defineProps<{
  isVisible: boolean;
}>();

const emit = defineEmits<{
  (e: "close"): void;
}>();

const onClose = () => emit("close");

/* Lock body scroll */
watch(
  () => props.isVisible,
  (val) => {
    document.body.style.overflow = val ? "hidden" : "";
  },
  { immediate: true }
);

function handleKeydown(e: KeyboardEvent) {
  if (e.key === "Escape") onClose();
}

onMounted(() => {
  window.addEventListener("keydown", handleKeydown);
});

onBeforeUnmount(() => {
  window.removeEventListener("keydown", handleKeydown);
  document.body.style.overflow = "";
});
</script>

<style scoped lang="scss">
.fu-preview-backdrop {
  position: fixed;
  inset: 0;
  z-index: 1200;
  background: rgba(0, 0, 0, 0.55);
}

