<template>
  <div class="fu-activity-item">
    <div class="fu-activity-icon">
      <component :is="icon" class="fu-activity-icon__svg" :size="18" />
    </div>

    <div class="fu-activity-line"></div>

    <div class="fu-activity-content">
      <div
        class="fu-activity-card"
        :class="{ 'is-clickable': clickable }"
        @click="handleClick"
      >
        <div class="fu-activity-header">
          <div class="fu-activity-title">
            <span class="fu-activity-status">
              <CheckCircle class="fu-activity-status__icon" :size="16" />
            </span>
            <p class="fu-activity-text">{{ title }}</p>
          </div>
        </div>

        <p class="fu-activity-subtitle">{{ fileName }}</p>

        <div class="fu-activity-footer">
          <span class="fu-activity-timestamp">{{ timestamp }}</span>

          <div class="fu-activity-user">
            <FuAvatar
              :src="userAvatar || undefined"
              :name="userName"
              :alt="userName"
              size="xs"
              :show-status="false"
            />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import type { Component } from "vue";
import { CheckCircle } from "lucide-vue-next";
import FuAvatar from "../../avatar/FuAvatar.vue";

interface Props {
  icon: Component;
  title: string;
  fileName: string;
  timestamp: string;
  userName: string;
  userAvatar?: string | null;
  clickable?: boolean;
}

const props = withDefaults(defineProps<Props>(), {
  userAvatar: null,
  clickable: true,
});

const emit = defineEmits<{
  (e: "click"): void;
}>();

function handleClick() {
  if (!props.clickable) return;
  emit("click");
}
</script>

