<template>
  <!-- INLINE MODE -->
  <div v-if="mode === 'inline'" class="efw-wrapper" ref="inlineRef">
    <div v-if="!isEditing" class="efw-read">
      <slot name="read" />
    </div>

    <div v-else class="efw-edit">
      <slot name="edit" :model="editableValue" />
      <div class="efw-footer">
        <slot name="actions" />
      </div>
    </div>
  </div>

  <!-- TELEPORT MODE -->
  <template v-else>
    <div class="efw-read">
      <slot name="read" />
    </div>

    <Teleport :to="teleportTo">
      <div
        v-if="isEditing"
        class="efw-teleport-card"
        ref="teleportRef"
        :style="{ top: position.top + 'px', left: position.left + 'px' }"
      >
        <slot name="edit" :model="editableValue" />
        <div class="efw-footer">
          <slot name="actions" />
        </div>
      </div>
    </Teleport>
  </template>
</template>

<script setup lang="ts">
import { ref, reactive, nextTick, onMounted, onBeforeUnmount, toRaw } from "vue";

interface EditableWrapperProps<T = any> {
  modelValue: T;
  mode?: "inline" | "teleport";
  teleportTo?: string;
  align?: "left" | "center" | "right";
  disableOutsideClose?: boolean; // âœ… NEW
}

const props = withDefaults(defineProps<EditableWrapperProps>(), {
  modelValue: () => ({}),
  mode: "inline",
  teleportTo: "body",
  align: "right",
  disableOutsideClose: false, // âœ… default
});

const isEditing = ref(false);
const editableValue = ref<any>(null);
const position = reactive({ top: 0, left: 0 });

const inlineRef = ref<HTMLElement | null>(null);
const teleportRef = ref<HTMLElement | null>(null);

/* -------------------------------------------------
   SAFE CLONE
-------------------------------------------------- */
function safeClone(value: any) {
  if (value === null || typeof value !== "object") return value;

  const raw = toRaw(value);

  if (Array.isArray(raw)) return [...raw];
  if (raw.constructor === Object) return { ...raw };

  return raw;
}

/* -------------------------------------------------
   OPEN INLINE
-------------------------------------------------- */
function startEditing() {
  document.dispatchEvent(new CustomEvent("close-all-editors"));
  editableValue.value = safeClone(props.modelValue);
  isEditing.value = true;
}

/* -------------------------------------------------
   OPEN TELEPORT
-------------------------------------------------- */
function openTeleport(event?: MouseEvent) {
  if (isEditing.value) {
    closeEditor();
    return;
  }

  document.dispatchEvent(new CustomEvent("close-all-editors"));
  editableValue.value = safeClone(props.modelValue);

  nextTick(() => {
    isEditing.value = true;

    nextTick(() => {
      const trigger = event?.currentTarget as HTMLElement;
      if (!trigger || !teleportRef.value) return;

      const rect = trigger.getBoundingClientRect();
      const popupWidth = teleportRef.value.offsetWidth;

      position.top = rect.bottom + 6 + window.scrollY;

      if (props.align === "left") {
        position.left = rect.left + window.scrollX;
      } else if (props.align === "center") {
        position.left = rect.left + rect.width / 2 - popupWidth / 2 + window.scrollX;
      } else {
        position.left = rect.right - popupWidth + window.scrollX;
      }
    });
  });
}

/* -------------------------------------------------
   CLOSE
-------------------------------------------------- */
function closeEditor() {
  isEditing.value = false;
}

/* -------------------------------------------------
   GLOBAL CLOSE
-------------------------------------------------- */
function handleGlobalClose() {
  if (isEditing.value) closeEditor();
}

/* -------------------------------------------------
   POINTER DOWN OUTSIDE
-------------------------------------------------- */
function handlePointerDown(event: PointerEvent) {
  if (!isEditing.value) return;

  // âœ… HARD STOP: autocomplete-safe mode
  if (props.disableOutsideClose) return;

  const target = event.target as HTMLElement;

  // ignore known dropdown overlays
  if (
    target.closest(
      ".fu-status-dropdown, .fu-status-dropdown__menu, .fu-autocomplete-dropdown, .fu-select-dropdown, .fu-datepicker-dropdown"
    )
  ) {
    return;
  }

  if (props.mode === "inline") {
    if (inlineRef.value && !inlineRef.value.contains(target)) {
      closeEditor();
    }
  } else {
    if (teleportRef.value && !teleportRef.value.contains(target)) {
      closeEditor();
    }
  }
}

/* -------------------------------------------------
   LIFECYCLE
-------------------------------------------------- */
onMounted(() => {
  document.addEventListener("close-all-editors", handleGlobalClose);
  document.addEventListener("ew-close", closeEditor);
  document.addEventListener("pointerdown", handlePointerDown);
  window.addEventListener("resize", closeEditor);
});

onBeforeUnmount(() => {
  document.removeEventListener("close-all-editors", handleGlobalClose);
  document.removeEventListener("ew-close", closeEditor);
  document.removeEventListener("pointerdown", handlePointerDown);
  window.removeEventListener("resize", closeEditor);
});

/* -------------------------------------------------
   EXPOSE API
-------------------------------------------------- */
defineExpose({
  startEditing,
  openTeleport,
  closeEditor,
});
</script>

<style scoped>
.efw-wrapper {
  background: inherit;
  border-radius: 6px;
  display: flex;
  flex-direction: column;
}

.efw-read {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.efw-edit {
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-bottom: 1rem;
}

.efw-footer {
  margin-top: 12px;
  padding: 12px;
  border-top: 1px solid var(--fu-border);
  background: var(--fu-surface-background);
}

.efw-teleport-card {
  position: absolute;
  z-index: 9999;
  background: white;
  border: 1px solid var(--fu-border);
  border-radius: 8px;
