<template>
  <div
    class="fu-avatar"
    :class="[`fu-avatar--${size}`, { 'fu-avatar--editable': editable }]"
  >
    <div class="fu-avatar__wrapper" @click="onAvatarClick">
      <img v-if="src" :src="src" :alt="alt" class="fu-avatar__image" />
      <span v-else class="fu-avatar__placeholder">{{ initials }}</span>

      <!-- Edit overlay -->
      <span v-if="editable" class="fu-avatar__edit-overlay"> Edit </span>

      <!-- Remove button (only if image exists) -->
      <button
        v-if="editable && src && allowRemove"
        class="fu-avatar__remove"
        @click.stop="removeImage"
        aria-label="Remove photo"
      >
        Ã—
      </button>

      <!-- Status Dot -->
      <span
        v-if="showStatus && status !== undefined"
        class="fu-status-dot"
        :class="statusClass"
      ></span>

      <!-- Hidden file input -->
      <input
        ref="fileInput"
        type="file"
        accept="image/*"
        class="fu-avatar__file-input"
        @change="onFileChange"
      />
    </div>

    <!-- <span v-if="name" class="fu-avatar__name">{{ name }}</span> -->
  </div>
</template>

<script setup lang="ts">
import { computed, ref } from "vue";
import { UserStatus } from "./UserStatus";

const props = withDefaults(
  defineProps<{
    src?: string;
    alt?: string;
    name?: string;
    size?: "xs" | "sm" | "md" | "lg" | "xl" | "2xl" | "3xl";
    status?: UserStatus | number;
    showStatus?: boolean;
    editable?: boolean;
    allowRemove?: boolean;
  }>(),
  {
    size: "md",
    showStatus: true,
    editable: false,
    allowRemove: true,
  }
);

const emit = defineEmits<{
  (e: "update:src", value: string): void;
  (e: "remove"): void;
}>();

const fileInput = ref<HTMLInputElement | null>(null);

// Initials
const initials = computed(() =>
  props.name
    ? props.name
        .split(" ")
        .map((n) => n[0])
        .join("")
        .slice(0, 2)
        .toUpperCase()
    : ""
);

// Avatar click handler
const onAvatarClick = () => {
  if (!props.editable) return;
  fileInput.value?.click();
};

// Handle upload
const onFileChange = (e: Event) => {
  const file = (e.target as HTMLInputElement)?.files?.[0];
  if (!file) return;

  const reader = new FileReader();
  reader.onload = () => {
    emit("update:src", reader.result as string);
  };
  reader.readAsDataURL(file);
};

// Remove image
const removeImage = () => {
  emit("remove");
};

// Status class
const statusClass = computed(() => {
  switch (props.status) {
    case UserStatus.Active:
    case 1:
      return "fu-status-dot--active";
    case UserStatus.Away:
    case 2:
      return "fu-status-dot--away";
    case UserStatus.Busy:
    case 3:
      return "fu-status-dot--busy";
    case UserStatus.DoNotDisturb:
    case 4:
      return "fu-status-dot--dnd";
    case UserStatus.Invisible:
    case 5:
      return "fu-status-dot--invisible";
    default:
      return "fu-status-dot--offline";
  }
});
</script>

