<template>
  <div
    class="fu-split-button"
    :class="[sizeClass, variantClass]"
    :style="splitButtonStyle"
    ref="splitButtonRef"
  >
    <!-- Main button -->
    <button
      class="fu-split-button__main"
      :disabled="disabled"
      @click="mainAction"
      type="button"
      aria-haspopup="true"
      :aria-expanded="isOpen"
    >
      <component v-if="icon" :is="icon" class="fu-split-button__icon" />
      <span><slot /></span>
    </button>

    <!-- Toggle dropdown button -->
    <button
      class="fu-split-button__toggle"
      @click="toggleDropdown"
      :aria-expanded="isOpen"
      :disabled="disabled"
      type="button"
      aria-label="Toggle dropdown"
    >
      <ChevronDown />
    </button>

    <!-- Teleported dropdown -->
    <teleport to="body">
      <transition name="fade">
        <div
          v-if="isOpen"
          class="fu-split-button__dropdown"
          :class="[`fu-split-button__dropdown--${align}`]"
          ref="dropdownRef"
          :style="menuStyle"
        >
          <div
            v-for="option in options"
            :key="option.value"
            class="fu-split-button__option"
            @click="selectOption(option)"
          >
            <component
              v-if="option.type === 'icon'"
              :is="option.icon"
              class="fu-split-button__option-icon"
            />
            <img
              v-else-if="option.type === 'image'"
              :src="option.imageUrl"
              class="fu-split-button__option-image"
              alt=""
            />
            <span class="fu-split-button__option-label">{{ option.label }}</span>
          </div>
        </div>
      </transition>
    </teleport>
  </div>
</template>

<script setup lang="ts">
import { ref, computed, nextTick, onMounted, onBeforeUnmount } from "vue";
import { ChevronDown } from "lucide-vue-next";

interface Option {
  label: string;
  value: string | number;
  type?: "icon" | "image";
  icon?: any;
  imageUrl?: string;
}

const props = defineProps<{
  options: Option[];
  disabled?: boolean;
  size?: "sm" | "md" | "lg";
  variant?: "solid" | "subtle" | "outline" | "ghost" | "danger";
  color?: string;
  icon?: any;
  align?: "left" | "center" | "right";
  buttonWidth?: string;
}>();

const emit = defineEmits<{
  (e: "main-action"): void;
  (e: "select", option: Option): void;
}>();

const isOpen = ref(false);
const dropdownRef = ref<HTMLElement | null>(null);
const splitButtonRef = ref<HTMLElement | null>(null);

const align = computed(() => props.align ?? "right");
const menuStyle = ref<Record<string, string>>({});

const sizeClass = computed(() => `fu-split-button--${props.size ?? "sm"}`);
const variantClass = computed(() => `fu-split-button--${props.variant ?? "solid"}`);

// NEW: Combined style for wrapper
const splitButtonStyle = computed(() => ({
  ...(props.color ? { "--fu-split-bg": props.color } : {}),
  ...(props.buttonWidth ? { width: props.buttonWidth } : {}),
}));

// Toggle dropdown
function toggleDropdown() {
  if (props.disabled) return;
  isOpen.value = !isOpen.value;

  if (isOpen.value) {
    nextTick(() => {
      positionDropdown();
      window.addEventListener("click", onClickOutside);
      window.addEventListener("resize", positionDropdown);
    });
  }
}

// Position dropdown
function positionDropdown() {
  if (!splitButtonRef.value || !dropdownRef.value) return;

  const rect = splitButtonRef.value.getBoundingClientRect();
  const menuWidth = dropdownRef.value.offsetWidth;

  const positions: Record<string, string> = {
    top: `${rect.bottom + window.scrollY + 4}px`,
    left: `${rect.left + window.scrollX}px`,
  };

  if (align.value === "right") {
    positions.left = `${rect.right - menuWidth + window.scrollX}px`;
  } else if (align.value === "center") {
    positions.left = `${rect.left + rect.width / 2 - menuWidth / 2 + window.scrollX}px`;
  }

  menuStyle.value = {
    position: "absolute",
    ...positions,
    zIndex: "1000",
  };
}

// Close on outside click
function onClickOutside(e: MouseEvent) {
  if (
    splitButtonRef.value?.contains(e.target as Node) ||
    dropdownRef.value?.contains(e.target as Node)
  )
    return;

  isOpen.value = false;
  window.removeEventListener("click", onClickOutside);
  window.removeEventListener("resize", positionDropdown);
}

// Main button action
function mainAction() {
  if (!props.disabled) emit("main-action");
}

// Select dropdown option
function selectOption(option: Option) {
  emit("select", option);
  isOpen.value = false;
}

// Clean up
onMounted(() => {
  onBeforeUnmount(() => {
    window.removeEventListener("click", onClickOutside);
    window.removeEventListener("resize", positionDropdown);
  });
});
</script>

