<template>
  <div class="fu-time-picker" ref="inputRef">
    <FusionTextInput
      type="text"
      v-model="timeValue"
      placeholder="HH:mm or 4:30pm"
      @focus="
        timeOpen = true;
        calculatePosition();
      "
      @input="onTimeInput"
      @keydown.enter.prevent="applyTypedTime"
      @blur="onTimeBlur"
      :label="label"
      :error="error"
      :required="required"
      :disabled="disabled"
      :size="size"
      :formWrapperWidth="formWrapperWidth"
    >
      <template #right>
        <ChevronDown />
      </template>
    </FusionTextInput>

    <Teleport to="body">
      <div
        v-if="timeOpen"
        ref="dropdownRef"
        class="fu-time-dropdown customScrollBar"
        :class="{ 'open-up': openUpwards }"
        :style="{
          left: inputRect.left + 'px',
          width: inputRect.width + 'px',
          top: openUpwards
            ? inputRect.top - dropdownHeight + 'px'
            : inputRect.bottom + 'px',
        }"
      >
        <div
          v-for="(opt, index) in visibleTimeOptions"
          :key="opt.value"
          class="fu-time-option"
          :ref="(el) => setFirstVisible(el, index)"
          @mousedown.prevent="selectTime(opt.value)"
        >
          {{ opt.label }}
        </div>
      </div>
    </Teleport>
  </div>
</template>

<script setup>
import { ref, computed, onMounted, onBeforeUnmount, watch, nextTick } from "vue";
import FusionTextInput from "@/components/TextInput/FusionTextInput.vue";
import { ChevronDown } from "lucide-vue-next";

const props = defineProps({
  modelValue: String,
  interval: { type: Number, default: 30 },
  error: String,
  label: String,
  required: Boolean,
  disabled: Boolean,
  size: String,
  formWrapperWidth: String,
});

const emit = defineEmits(["update:modelValue", "change"]);

const timeValue = ref(props.modelValue || "");
const timeOpen = ref(false);
const openUpwards = ref(false);
const inputRef = ref(null);
const dropdownRef = ref(null);
const inputRect = ref({ left: 0, top: 0, bottom: 0, width: 0 });
const dropdownHeight = 240;
const firstVisibleOption = ref(null);

/* ------------------ Time options ------------------ */
const timeOptions = computed(() => {
  const opts = [];
  for (let h = 0; h < 24; h++) {
    for (let m = 0; m < 60; m += props.interval) {
      const date = new Date();
      date.setHours(h, m, 0);
      opts.push({
        label: date.toLocaleTimeString([], {
          hour: "numeric",
          minute: "2-digit",
          hour12: true,
        }),
        value: `${String(h).padStart(2, "0")}:${String(m).padStart(2, "0")}`,
      });
    }
  }
  return opts;
});

const filteredTimeOptions = computed(() =>
  timeValue.value
    ? timeOptions.value.filter((t) =>
        t.label.toLowerCase().includes(timeValue.value.toLowerCase())
      )
    : timeOptions.value
);

const visibleTimeOptions = computed(() =>
  filteredTimeOptions.value.length ? filteredTimeOptions.value : timeOptions.value
);

/* ------------------ Input handler ------------------ */
const onTimeInput = (e) => {
  let val = e.target.value.toLowerCase();
  val = val.replace(/[^0-9:apm]/g, "");

  const input = e.target;

  // Auto-insert colon after two digits
  if (/^\d{2}$/.test(val)) {
    val += ":";
    timeValue.value = val;
    nextTick(() => input.setSelectionRange(3, 3));
    return;
  }

  timeValue.value = val;
};

/* ------------------ Normalize ------------------ */
const normalizeTime = (val) => {
  if (!val) return null;
  const match = val
    .trim()
    .toLowerCase()
    .match(/^(\d{1,2})(?::(\d{2}))?\s?(am|pm)?$/);
  if (!match) return null;

  let hour = parseInt(match[1], 10);
  const min = parseInt(match[2] || "00", 10);
  const meridian = match[3];

  if (meridian === "pm" && hour < 12) hour += 12;
  if (meridian === "am" && hour === 12) hour = 0;

  if (hour > 23 || min > 59) return null;

  const hour12 = hour % 12 || 12;
  const displayMeridian = hour < 12 ? "am" : "pm";
  return `${String(hour12).padStart(2, "0")}:${String(min).padStart(
    2,
    "0"
  )}${displayMeridian}`;
};

/* ------------------ Dropdown ------------------ */
const calculatePosition = () => {
  if (!inputRef.value) return;
  const rect = inputRef.value.getBoundingClientRect();
  inputRect.value = {
    left: rect.left,
    top: rect.top,
    bottom: rect.bottom,
    width: rect.width,
  };
  const spaceBelow = window.innerHeight - rect.bottom;
  const spaceAbove = rect.top;
  openUpwards.value = spaceBelow < dropdownHeight && spaceAbove > spaceBelow;
};

const setFirstVisible = (el, index) => {
  if (index === 0 && el) firstVisibleOption.value = el;
};

const selectTime = (time) => {
  timeValue.value = time;
  emit("update:modelValue", time);
  emit("change", time);
  timeOpen.value = false;
};

const applyTypedTime = () => {
  const normalized = normalizeTime(timeValue.value);
  if (!normalized) return;
  timeValue.value = normalized;
  emit("update:modelValue", normalized);
  emit("change", normalized);
  timeOpen.value = false;
};

const onTimeBlur = () =>
  setTimeout(() => {
    applyTypedTime();
    timeOpen.value = false;
  }, 120);

/* ------------------ Watches ------------------ */
watch(
  () => props.modelValue,
  (v) => (timeValue.value = v || "")
);
watch(timeValue, async () => {
  await nextTick();
  if (firstVisibleOption.value)
    firstVisibleOption.value.scrollIntoView({ behavior: "smooth", block: "nearest" });
});

/* ------------------ Lifecycle ------------------ */
const onResize = () => calculatePosition();
onMounted(() => {
  window.addEventListener("resize", onResize);
  nextTick(calculatePosition);
});
onBeforeUnmount(() => window.removeEventListener("resize", onResize));
</script>

<style scoped lang="scss">
.fu-time-dropdown {
  position: fixed;
  max-width: 180px;
  max-height: 240px;
  overflow-y: auto;
  background: var(--fu-content-body);
  color: var(--fu-color-text);
  border: 0.5px solid var(--fu-border);
  border-radius: 4px;
  z-index: 9999;
  box-shadow: 0 -6px 12px rgba(0, 0, 0, 0.15);
}
.fu-time-option {
  padding: 8px 12px;
  cursor: pointer;
  user-select: none;
}
.fu-time-option:hover {
  background: var(--fu-surface-hover);
  color: var(--fu-color-text);
}
</style>
