<template>
  <Teleport to="body">
    <div v-if="isVisible" class="fu-modal__backdrop" @click.self="onCancel">
      <div class="fu-confirm" :class="`fu-confirm--${variant}`">
        <!-- BODY -->
        <div class="fu-confirm__body">
          <div class="fu-confirm__icon">
            <component :is="icon" />
          </div>

          <h3 class="fu-confirm__title">{{ title }}</h3>
          <p class="fu-confirm__message">{{ message }}</p>
        </div>

        <!-- FOOTER -->
        <div class="fu-confirm__footer">
          <FusionButton variant="outline" buttonWidth="100%" @click="onCancel">
            {{ cancelText }}
          </FusionButton>

          <FusionButton :variant="confirmVariant" buttonWidth="100%" @click="onConfirm">
            {{ confirmText }}
          </FusionButton>
        </div>
      </div>
    </div>
  </Teleport>
</template>

<script setup lang="ts">
import { computed, watch, onMounted, onBeforeUnmount } from "vue";
import FusionButton from "../button/FusionButton.vue";
import { Trash2, AlertTriangle, CheckCircle } from "lucide-vue-next";

const props = withDefaults(
  defineProps<{
    isVisible: boolean;
    title: string;
    message: string;
    variant?: "delete" | "warning" | "confirm";
    confirmText?: string;
    cancelText?: string;
  }>(),
  {
    variant: "confirm",
    confirmText: "Confirm",
    cancelText: "Cancel",
  }
);

const emit = defineEmits<{
  (e: "cancel"): void;
  (e: "confirm"): void;
}>();

const onCancel = () => emit("cancel");
const onConfirm = () => emit("confirm");

/* â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€ ICONS â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€ */

const icon = computed(() => {
  if (props.variant === "delete") return Trash2;
  if (props.variant === "warning") return AlertTriangle;
  return CheckCircle;
});

const confirmVariant = computed(() => {
  if (props.variant === "delete") return "danger";
  if (props.variant === "warning") return "danger";
  return "solid";
});

/* â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€ KEYBOARD (ENTER â†’ CONFIRM) â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€ */

/**
 * Only triggers when dialog is visible
 * Prevents accidental confirms elsewhere
 */
const handleKeydown = (e: KeyboardEvent) => {
  if (!props.isVisible) return;

  if (e.key === "Enter") {
    e.preventDefault();
    onConfirm();
  }

  if (e.key === "Escape") {
    onCancel();
  }
};

onMounted(() => {
  window.addEventListener("keydown", handleKeydown);
});

onBeforeUnmount(() => {
  window.removeEventListener("keydown", handleKeydown);
});
</script>

