<template>
  <div class="fu-filter-dropdown" ref="dropdown">
    <!-- Trigger -->
    <div class="fu-filter-dropdown__trigger" @click="toggleDropdown">
      <slot name="trigger" />
    </div>

    <!-- Teleported Menu -->
    <teleport to="body">
      <transition name="fade">
        <div
          v-if="dropdownOpen"
          ref="menuRef"
          class="fu-filter-dropdown__menu"
          :class="[`fu-filter-dropdown__menu--${align}`]"
          :style="[menuPosition, menuSize]"
        >
          <!-- Header -->
          <div v-if="title" class="fu-filter-dropdown__header">
            <span class="fu-filter-dropdown__title">
              {{ title }}
            </span>

            <!-- <button
              type="button"
              class="fu-filter-dropdown__close"
              @click="closeDropdown"
              aria-label="Close"
            >
              Ã—
            </button> -->
            <FusionActionButton
              size="sm"
              variant="subtle"
              :icon="X"
              @click="closeDropdown"
            />
          </div>

          <!-- Body -->
          <div class="fu-filter-dropdown__body">
            <slot name="content" />
          </div>

          <!-- Footer -->
          <div class="fu-filter-dropdown__footer">
            <slot name="footer">
              <!-- <button type="button" class="fu-btn fu-btn--outline" @click="onCancel">
                Cancel
              </button> -->
              <FusionButton variant="subtle" size="sm" text="Cancel" @click="onCancel" />
              <FusionButton variant="solid" size="sm" text="Apply" @click="onApply" />
              <!-- <button type="button" class="fu-btn fu-btn--primary" @click="onApply">
                Apply
              </button> -->
            </slot>
          </div>
        </div>
      </transition>
    </teleport>
  </div>
</template>
<script setup lang="ts">
import { ref, watch, onMounted, onBeforeUnmount, nextTick, computed } from "vue";
import type { CSSProperties } from "vue";
import FusionButton from "../button/FusionButton.vue";
import FusionActionButton from "@/components/actionButton/FusionActionButton.vue";
import { X } from "lucide-vue-next";

interface Props {
  align?: "left" | "center" | "right";
  isOpen?: boolean;
  payload?: unknown;
  title?: string;
  width?: number | string;
  maxWidth?: number | string;
}

const props = withDefaults(defineProps<Props>(), {
  align: "left",
  isOpen: undefined,
  title: "",
  width: 280,
  maxWidth: 360,
});

const emit = defineEmits<{
  (e: "apply", payload: unknown): void;
  (e: "cancel"): void;
  (e: "open"): void;
  (e: "close"): void;
  (e: "update:isOpen", value: boolean): void;
}>();

const dropdownOpen = ref(false);
const dropdown = ref<HTMLElement | null>(null);
const menuRef = ref<HTMLElement | null>(null);

const menuPosition = ref<CSSProperties>({
  top: "0px",
  left: "0px",
});

/* -----------------------------
   Size styles
------------------------------ */
const menuSize = computed<CSSProperties>(() => ({
  width: typeof props.width === "number" ? `${props.width}px` : props.width,
  maxWidth: typeof props.maxWidth === "number" ? `${props.maxWidth}px` : props.maxWidth,
}));

/* -----------------------------
   Sync external control
------------------------------ */
watch(
  () => props.isOpen,
  (val) => {
    if (typeof val === "boolean") {
      dropdownOpen.value = val;

      if (val) {
        emit("open");
        nextTick(positionMenu);
      } else {
        emit("close");
      }
    }
  }
);

/* -----------------------------
   Toggle
------------------------------ */
function toggleDropdown(e?: MouseEvent) {
  e?.stopPropagation();

  const willOpen = !dropdownOpen.value;

  if (willOpen) {
    document.dispatchEvent(new CustomEvent("close-all-dropdowns"));
  }

  dropdownOpen.value = willOpen;
  emit("update:isOpen", willOpen);

  if (willOpen) {
    emit("open");
    nextTick(positionMenu);
  } else {
    emit("close");
  }
}

/* -----------------------------
   Actions
------------------------------ */
function onApply() {
  emit("apply", props.payload);
  closeDropdown();
}

function onCancel() {
  emit("cancel");
  closeDropdown();
}

/* -----------------------------
   Close helpers
------------------------------ */
function closeDropdown() {
  if (!dropdownOpen.value) return;

  dropdownOpen.value = false;
  emit("update:isOpen", false);
  emit("close");
}

/* -----------------------------
   Positioning
------------------------------ */
function positionMenu() {
  if (!dropdown.value || !menuRef.value) return;

  const rect = dropdown.value.getBoundingClientRect();
  const top = rect.bottom + window.scrollY + 8;
  let left = rect.left + window.scrollX;

  if (props.align === "center") {
    left += rect.width / 2 - menuRef.value.offsetWidth / 2;
  }

  if (props.align === "right") {
    left = rect.right - menuRef.value.offsetWidth + window.scrollX;
  }

  menuPosition.value = {
    position: "absolute",
    top: `${top}px`,
    left: `${left}px`,
    zIndex: 2000,
  };
}

/* -----------------------------
   Outside / Global close
------------------------------ */
function handleClickOutside(e: MouseEvent) {
  if (
    dropdownOpen.value &&
    dropdown.value &&
    !dropdown.value.contains(e.target as Node) &&
    menuRef.value &&
    !menuRef.value.contains(e.target as Node)
  ) {
    closeDropdown();
  }
}

function handleGlobalClose() {
  closeDropdown();
}

onMounted(() => {
  // document.addEventListener("click", handleClickOutside);
  // document.addEventListener("close-all-dropdowns", handleGlobalClose);
});

onBeforeUnmount(() => {
  document.removeEventListener("click", handleClickOutside);
  document.removeEventListener("close-all-dropdowns", handleGlobalClose);
});
</script>
