  justify-content: space-between;
  padding: 0.75rem 1rem;
  border-bottom: 1px solid var(--fu-border);
}

.fu-filter-dropdown__title {
  font-size: 14px;
  font-weight: 500;
}

/* Body */
.fu-filter-dropdown__body {
  padding: 0.75rem 1rem;
}

/* Footer */
.fu-filter-dropdown__footer {
  display: flex;
  justify-content: flex-end;
  gap: 0.5rem;
  padding: 0.75rem 1rem;
