<template>
  <div
    :class="['fu-date-picker', variantClass]"
    ref="pickerRef"
    :style="{ width: formWrapperWidth }"
  >
    <FusionTextInput
      v-if="variant !== 'date-range'"
      type="text"
      :modelValue="displayValue"
      placeholder="Select date"
      readonly
      @click="toggleCalendar"
      :formWrapperWidth="formWrapperWidth"
      :size="size"
      :error="error"
      :required="required"
      :label="label"
      :disabled="disabled"
    >
      <template #right>
        <ChevronDown />
      </template>
    </FusionTextInput>

    <FusionTextInput
      v-else
      type="text"
      :modelValue="displayRange"
      placeholder="Select date range"
      @click="toggleCalendar"
      readonly
      :formWrapperWidth="formWrapperWidth"
      :size="size"
      :error="error"
      :required="required"
      :disabled="disabled"
    >
      <template #right>
        <ChevronDown />
      </template>
    </FusionTextInput>

    <teleport to="body">
      <div
        v-if="calendarOpen"
        class="fu-date-picker__calendar-overlay"
        @click.self="closeCalendar"
      >
        <div
          class="fu-date-picker__calendar"
          :style="calendarPosition"
          ref="calendarRef"
          @click.stop
        >
          <!-- Header -->
          <div class="calendar-header">
            <FusionButton variant="ghost" size="sm" @click="toggleView">
              <template v-if="viewMode === 'days'">
                {{ currentMonth.format("MMMM YYYY") }}
              </template>
              <template v-else-if="viewMode === 'months'">
                {{ currentMonth.year() }}
              </template>
              <template v-else> {{ yearRangeStart }} - {{ yearRangeEnd }} </template>
            </FusionButton>

            <div class="flex flex--gap-sm">
              <FusionActionButton :icon="ChevronLeft" size="sm" @click="prevAction" />
              <FusionActionButton :icon="ChevronRight" size="sm" @click="nextAction" />
            </div>
          </div>

          <!-- Days -->
          <div v-if="viewMode === 'days'">
            <div class="calendar-weekdays">
              <div v-for="d in weekdays" :key="d" class="calendar-weekday">{{ d }}</div>
            </div>

            <div class="calendar-days">
              <div
                v-for="day in daysInCalendar"
                :key="day.date.toString()"
                class="calendar-day"
                :class="{
                  'calendar-day--other-month': !day.isCurrentMonth,
                  'calendar-day--selected': isSelected(day.date),
                  'calendar-day--in-range': isInRange(day.date),
                  'calendar-day--disabled': isDisabled(day.date),
                }"
                @click="selectDate(day.date)"
              >
                {{ day.date.date() }}
              </div>
            </div>
          </div>

          <!-- Months -->
          <div v-else-if="viewMode === 'months'" class="calendar-months">
            <div
              v-for="(m, i) in months"
              :key="m"
              class="calendar-month"
              :class="{ 'calendar-month--selected': i === currentMonth.month() }"
              @click="selectMonth(i)"
            >
              {{ m }}
            </div>
          </div>

          <!-- Years -->
          <div v-else class="calendar-years">
            <div
              v-for="year in yearsRange"
              :key="year"
              class="calendar-year"
              :class="{ 'calendar-year--selected': year === currentMonth.year() }"
              @click="selectYear(year)"
            >
              {{ year }}
            </div>
          </div>

          <hr />

          <!-- Quick actions -->
          <div class="flex flex--space flex--gap-md px-2 pb-2">
            <div class="flex flex--gap-sm" v-if="variant !== 'date-range'">
              <FusionButton variant="outline" @click="selectToday">Today</FusionButton>
              <FusionButton variant="outline" @click="selectTomorrow"
                >Tomorrow</FusionButton
              >
            </div>
            <div v-if="variant === 'date-range'">
              <FusionButton variant="outline" @click="applyRange">Apply</FusionButton>
            </div>
            <FusionButton variant="outline" @click="clearSelection">Clear</FusionButton>
          </div>

          <!-- Time -->
          <div v-if="variant === 'date-time'" class="calendar-time">
            <div class="fu-time-input-wrapper">
              <!-- <input
                type="text"
                class="fu-time-input"
                v-model="timeValue"
                placeholder="HH:mm or 4:30pm"
                @focus="timeOpen = true"
                @keydown.enter.prevent="applyTypedTime"
                @blur="onTimeBlur"
              /> -->
              <FusionTextInput
                type="text"
                v-model="timeValue"
                placeholder="HH:mm or 4:30pm"
                @focus="timeOpen = true"
                @keydown.enter.prevent="applyTypedTime"
                @blur="onTimeBlur"
                formWrapperWidth="100%"
              >
                <template #right>
                  <ChevronDown />
                </template>
              </FusionTextInput>

              <div v-if="timeOpen" class="fu-time-dropdown customScrollBar">
                <div
                  v-for="opt in filteredTimeOptions"
                  :key="opt"
                  class="fu-time-option"
                  @mousedown.prevent="selectTime(opt)"
                >
                  {{ opt }}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </teleport>
  </div>
</template>

<script setup>
import { ref, computed, watch, nextTick, onBeforeUnmount } from "vue";
import dayjs from "dayjs";
import customParseFormat from "dayjs/plugin/customParseFormat";
import FusionButton from "@/components/button/FusionButton.vue";
import FusionActionButton from "@/components/actionButton/FusionActionButton.vue";
import { ChevronLeft, ChevronRight, ChevronDown } from "lucide-vue-next";
import FusionTextInput from "@/components/TextInput/FusionTextInput.vue";

dayjs.extend(customParseFormat);

const props = defineProps({
  modelValue: [String, Object],
  variant: {
    type: String,
    default: "date",
    validator: (v) => ["date", "date-time", "date-range"].includes(v),
  },
  min: String,
  max: String,

  // New props
  formWrapperWidth: String,
  size: {
    type: String,
    default: "md",
    validator: (v) => ["sm", "md", "lg"].includes(v),
  },
  error: {
    type: [String, null],
    default: null,
  },

  label: {
    type: String,
    default: null,
  },
  required: {
    type: Boolean,
    default: false,
  },
  disabled: {
    type: Boolean,
    default: false,
  },
});

const timeOpen = ref(false);
const emit = defineEmits(["update:modelValue"]);

const calendarOpen = ref(false);
const activeInput = ref(null);
const pickerRef = ref(null);
const calendarRef = ref(null);
const currentMonth = ref(dayjs().startOf("month"));

const selectedDate = ref(null);
const selectedRange = ref({ start: null, end: null });
const timeValue = ref("00:00");

const weekdays = ["Su", "Mo", "Tu", "We", "Th", "Fr", "Sa"];
const months = [
  "Jan",
  "Feb",
  "Mar",
  "Apr",
  "May",
  "Jun",
  "Jul",
  "Aug",
  "Sep",
  "Oct",
  "Nov",
  "Dec",
];
const viewMode = ref("days");

const yearsPerPage = 12;
const yearRangeStart = computed(
  () => Math.floor(currentMonth.value.year() / yearsPerPage) * yearsPerPage
);
const yearRangeEnd = computed(() => yearRangeStart.value + yearsPerPage - 1);
const yearsRange = computed(() =>
  Array.from({ length: yearsPerPage }, (_, i) => yearRangeStart.value + i)
);

const daysInCalendar = computed(() => {
  const start = currentMonth.value.startOf("month").startOf("week");
  const end = currentMonth.value.endOf("month").endOf("week");
  const days = [];
  let d = start.clone();
  while (d.isBefore(end) || d.isSame(end, "day")) {
    days.push({
      date: d.clone(),
      isCurrentMonth: d.month() === currentMonth.value.month(),
    });
    d = d.add(1, "day");
  }
  return days;
});

function isDisabled(date) {
  if (props.min && date.isBefore(dayjs(props.min), "day")) return true;
  if (props.max && date.isAfter(dayjs(props.max), "day")) return true;
  return false;
}

function isSelected(date) {
  if (props.variant === "date-range") {
    return (
      (selectedRange.value.start && date.isSame(selectedRange.value.start, "day")) ||
      (selectedRange.value.end && date.isSame(selectedRange.value.end, "day"))
    );
  }
  return selectedDate.value && date.isSame(selectedDate.value, "day");
}

function isInRange(date) {
  return (
    props.variant === "date-range" &&
    selectedRange.value.start &&
    selectedRange.value.end &&
    date.isAfter(selectedRange.value.start, "day") &&
    date.isBefore(selectedRange.value.end, "day")
  );
}

function selectDate(date) {
  if (isDisabled(date)) return;

  if (props.variant === "date-range") {
    if (!selectedRange.value.start || activeInput.value === "start") {
      selectedRange.value.start = date.clone();
      selectedRange.value.end = null;
      activeInput.value = "end";
    } else {
      if (date.isBefore(selectedRange.value.start, "day")) {
        selectedRange.value.end = selectedRange.value.start.clone();
        selectedRange.value.start = date.clone();
      } else {
        selectedRange.value.end = date.clone();
      }
      // Don't close calendar yet â€” wait for apply
    }
    return;
  }

  selectedDate.value = date.clone();
  emit(
    "update:modelValue",
    props.variant === "date-time"
      ? selectedDate.value.format("YYYY-MM-DDTHH:mm")
      : selectedDate.value.format("YYYY-MM-DD")
  );
  closeCalendar();
}

function applyRange() {
  if (selectedRange.value.start && selectedRange.value.end) {
    emit("update:modelValue", {
      start: selectedRange.value.start.format("YYYY-MM-DD"),
      end: selectedRange.value.end.format("YYYY-MM-DD"),
    });
    closeCalendar();
  }
}

function toggleCalendar(input = null) {
  activeInput.value = input;
  calendarOpen.value = true;

  if (props.variant === "date-range") {
    if (selectedRange.value.start) {
      currentMonth.value = selectedRange.value.start.startOf("month");
    } else {
      currentMonth.value = dayjs().startOf("month");
    }
  } else if (selectedDate.value) {
    currentMonth.value = selectedDate.value.startOf("month");
  } else {
    currentMonth.value = dayjs().startOf("month");
  }

  nextTick(() => {
    positionCalendar();
    window.addEventListener("resize", positionCalendar);
    window.addEventListener("click", onClickOutside);
  });
}

function closeCalendar() {
  calendarOpen.value = false;
  window.removeEventListener("resize", positionCalendar);
  window.removeEventListener("click", onClickOutside);
}

function onClickOutside(event) {
  if (
    !pickerRef.value?.contains(event.target) &&
    !calendarRef.value?.contains(event.target)
  ) {
    closeCalendar();
  }
}

const calendarPosition = ref({
  position: "absolute",
  top: "0px",
  left: "0px",
  zIndex: 9999,
});

function positionCalendar() {
  if (!pickerRef.value || !calendarRef.value) return;

  const pickerRect = pickerRef.value.getBoundingClientRect();
  const calendarRect = calendarRef.value.getBoundingClientRect();

  const spaceBelow = window.innerHeight - pickerRect.bottom;
  const spaceAbove = pickerRect.top;

  const scrollY = window.scrollY || window.pageYOffset;
  const scrollX = window.scrollX || window.pageXOffset;

  let top;

  if (spaceBelow < calendarRect.height && spaceAbove > calendarRect.height) {
    // Not enough space below, but enough above - open upwards
    top = pickerRect.top + scrollY - calendarRect.height - 6; // 6px gap
  } else {
    // Default: open downwards
    top = pickerRect.bottom + scrollY + 6;
  }

  calendarPosition.value = {
    position: "absolute",
    top: `${top}px`,
    left: `${pickerRect.left + scrollX}px`,
    zIndex: 9999,
  };
}

const displayValue = computed(() => {
  if (!selectedDate.value) return "";
  if (props.variant === "date-time") return selectedDate.value.format("YYYY-MM-DD HH:mm");
  return selectedDate.value.format("YYYY-MM-DD");
});

const displayRange = computed(() => {
  if (props.variant !== "date-range") return "";
  if (selectedRange.value.start && selectedRange.value.end) {
    return `${selectedRange.value.start.format(
      "YYYY-MM-DD"
    )} to ${selectedRange.value.end.format("YYYY-MM-DD")}`;
  }
  if (selectedRange.value.start) {
    return `${selectedRange.value.start.format("YYYY-MM-DD")} to ...`;
  }
  return "";
});

const variantClass = computed(() => `fu-date-picker--${props.variant}`);

watch(
  () => props.modelValue,
  (val) => {
    if (props.variant === "date-range") {
      // ... handle range ...
      return;
    }

    if (typeof val === "string" && val) {
      const parsed = dayjs(val);

      if (parsed.isValid()) {
        selectedDate.value = parsed;
        currentMonth.value = parsed.startOf("month");

        if (props.variant === "date-time") {
          timeValue.value = parsed.format("h:mm A"); // <-- sets initial time here
        } else {
          timeValue.value = "00:00";
        }

        return;
      }
    }

    if (val === null || val === "") {
      selectedDate.value = null;
      selectedRange.value = { start: null, end: null };
      timeValue.value = "00:00";
    }
  },
  { immediate: true }
);

function toggleView() {
  if (viewMode.value === "days") viewMode.value = "months";
  else if (viewMode.value === "months") viewMode.value = "years";
  else viewMode.value = "days";
}

function prevAction() {
  if (viewMode.value === "days")
    currentMonth.value = currentMonth.value.subtract(1, "month");
  else if (viewMode.value === "months")
    currentMonth.value = currentMonth.value.subtract(1, "year");
  else currentMonth.value = currentMonth.value.subtract(yearsPerPage, "year");
}

function nextAction() {
  if (viewMode.value === "days") currentMonth.value = currentMonth.value.add(1, "month");
  else if (viewMode.value === "months")
    currentMonth.value = currentMonth.value.add(1, "year");
  else currentMonth.value = currentMonth.value.add(yearsPerPage, "year");
}

function selectMonth(i) {
  currentMonth.value = currentMonth.value.month(i);
  viewMode.value = "days";
}

function selectYear(year) {
  currentMonth.value = currentMonth.value.year(year);
  viewMode.value = "months";
}

function selectToday() {
  const today = dayjs();
  if (props.variant === "date-range") {
    selectedRange.value = { start: today.clone(), end: today.clone() };
  } else {
    selectedDate.value = today.clone();
    emit(
      "update:modelValue",
      props.variant === "date-time"
        ? today.format("YYYY-MM-DDTHH:mm")
        : today.format("YYYY-MM-DD")
    );
    closeCalendar();
  }
}

function selectTomorrow() {
  const tomorrow = dayjs().add(1, "day");
  if (props.variant === "date-range") {
    selectedRange.value = { start: tomorrow.clone(), end: tomorrow.clone() };
  } else {
    selectedDate.value = tomorrow.clone();
    emit(
      "update:modelValue",
      props.variant === "date-time"
        ? tomorrow.format("YYYY-MM-DDTHH:mm")
        : tomorrow.format("YYYY-MM-DD")
    );
    closeCalendar();
  }
}

function clearSelection() {
  selectedDate.value = null;
  selectedRange.value = { start: null, end: null };
  emit(
    "update:modelValue",
    props.variant === "date-range" ? { start: null, end: null } : null
  );
  closeCalendar();
}

function onTimeChange(value) {
  const timeStr = typeof value === "string" ? value : timeValue.value;

  const parsedTime = dayjs(timeStr, ["H:mm", "h:mm A", "h:mmA", "ha", "h A", "H"], true);

  if (parsedTime.isValid() && selectedDate.value) {
    selectedDate.value = selectedDate.value
      .hour(parsedTime.hour())
      .minute(parsedTime.minute());

    // Normalize to 24h format string
    timeValue.value = selectedDate.value.format("HH:mm");

    emit("update:modelValue", selectedDate.value.format("YYYY-MM-DDTHH:mm"));
  }
}

// Friendly 12h time options for autocomplete
const timeOptions = computed(() => {
  const list = [];
  for (let h = 0; h < 24; h++) {
    for (let m = 0; m < 60; m += 15) {
      list.push(dayjs().hour(h).minute(m).format("h:mm A"));
    }
  }
  return list;
});

const filteredTimeOptions = computed(() => {
  if (!timeValue.value) return timeOptions.value;

  const q = timeValue.value.toLowerCase().replace(/\s+/g, "");

  return timeOptions.value.filter((t) =>
    t.toLowerCase().replace(/\s+/g, "").startsWith(q)
  );
});

function applyTypedTime() {
  if (!selectedDate.value || !timeValue.value) return;

  const raw = String(timeValue.value).trim().toLowerCase();

  const parsed = dayjs(
    raw,
    ["h:mm a", "h:mma", "ha", "h a", "hh:mm a", "H:mm", "HH:mm", "H"],
    true
  );

  if (!parsed.isValid()) {
    timeOpen.value = false;
    return;
  }

  selectedDate.value = selectedDate.value.hour(parsed.hour()).minute(parsed.minute());

  // Normalize DISPLAY to 12h AM/PM
  timeValue.value = selectedDate.value.format("h:mm A");

  emit("update:modelValue", selectedDate.value.format("YYYY-MM-DDTHH:mm"));
  timeOpen.value = false;
}

function selectTime(val) {
  timeValue.value = val;
  applyTypedTime();
}
function onTimeBlur() {
  setTimeout(() => {
    applyTypedTime();
    timeOpen.value = false;
  }, 120);
}

onBeforeUnmount(() => {
  window.removeEventListener("resize", positionCalendar);
  window.removeEventListener("click", onClickOutside);
});
</script>

